 .backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(32, 13, 29, 0.6);
    z-index: 100;
    opacity: 1;
  
    transition: opacity 500ms var(--timing-function);
  }

  .backdrop.is-hidden {
    opacity: 0;
    pointer-events: none;
  }
  
  .backdrop.is-hidden .modal {
    transform: translate(-50%, -50%) scale(0.5);
  }

  .modal {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 90%;
    padding-top: 75px;
    padding-bottom: 104px;
    border-radius: 150px 0px;
    background-color: var(--modal-bg-color);
    
    transform: translate(-50%, -50%) scale(1);
    transition: transform 500ms var(--timing-function);

    @media screen and (min-width: 320px) {
        width: 272px;
    }

    @media screen and (min-width: 768px) {
        width: 595px;
        padding-top: 28px;
        padding-bottom: 56px;
        border-radius: 300px 0px;
    }

    @media screen and (min-width: 1280px) {
        width: 930px;
        padding: 62px 0px;
    }

  }

.modal__close-button {
  position: absolute;
  top: 25px;
  right: 25px;

  display: flex;
  justify-content: center;
  align-items: center;

  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.modal__icon-close {
    fill: var(--btn-color);
    @media screen and (min-width: 1280px) {
        width: 25px;
        height: 25px;
    }
}

.modal__preview-img {
    margin-left: auto;
    margin-right: auto;
    width: 102px;
    height: 102px;
    border-radius: 50px;

    @media screen and (min-width: 768px) {
        width: 70px;
        height: 70px;
    }

    @media screen and (min-width: 1280px) {
        width: 132px;
        height: 132px;
        border-radius: 100px;
    }
}

.modal__data {
    text-align: center;
    margin-top: 18px;
    padding-left: 12px;
    padding-right: 17px;

    @media screen and (min-width: 768px) {
        display: flex;
        text-align: left;
        padding-left: 50px;
        margin-top: 43px;
    }

    @media screen and (min-width: 1280px) {
        padding-left: 30px;
        padding-right: 30px;
        margin-top: 41px;
    }
}

.modal__image {
    display: none;

    @media screen and (min-width: 768px) {
        display: block;
        width: 233px;
        height: 416px;
    }

    @media screen and (min-width: 1280px) {
        width: 360px;
        height: 641px;
    }
}

.modal__info {
    @media screen and (min-width: 768px) {
        margin-left: 30px;
    }
    @media screen and (min-width: 1280px) {
        margin-left: 60px;
    }
}

.modal__info-list-item + .modal__info-list-item {
    margin-top: 10px;
    @media screen and (min-width: 1280px) {
        margin-top: 20px;
    }
}

.modal__title {
    margin-bottom: 5px;
    font-weight: 600;
    font-size: 20px;
    line-height: 1.2;    
    color: var(--main-text-color);

    @media screen and (min-width: 1280px) {
        margin-bottom: 7px;
        font-size: 24px;
        line-height: 1.21;
    }
}

.modal__text {
    color: var(--modal-text-color);

    @media screen and (min-width: 1280px) {
        font-size: 18px;
        line-height: 1.22;
    }
}
.modal__text + .modal__text {
    margin-top: 3px;
    @media screen and (min-width: 1280px) {
        margin-top: 5px;
    }
}

.modal__prices-list-item + .modal__prices-list-item {
    margin-top: 20px;
    @media screen and (min-width: 1280px) {
        margin-top: 31px;
    }
}

.modal__prices-list-box {
    display: flex;
    justify-content: center;
    align-items: center;

    @media screen and (min-width: 768px) {
        justify-content: start;
    }
}

.modal__text--prices-list {
    margin-left: 13px;
}

.modal__prices-list-button {
    margin-top: 10px;
    padding: 9px 15px 11px;
    width: 145px;
    height: 40px;

    font-weight: 500;
    // background-color: var(--btn-color);
    background-color: var(--main-text-color);
    border-radius: 5px;
    color: var(--white-text-color);
    border: 1px solid var(--main-text-color);
    cursor: pointer;
    
    @media screen and (min-width: 1280px) {
        margin-top: 20px;
        font-size: 16px;
        line-height: 1.25;
    }
}

.modal__prices-list-button:hover,
.modal__prices-list-button:focus {
    background-color: var(--white-text-color);
    color: var(--main-text-color);
    transition: color var(--animation), background-color var(--animation);
}


.modal__more-button {
    display: block;
    margin-top: 25px;
    margin-left: auto;
    margin-right: auto;
    padding: 0;
    width: 230px;
    height: 40px;
    border: 1px solid var(--main-text-color);
    border-radius: 5px;
    font-weight: 500;
    color: var(--main-text-color);
    background-color: transparent;
    cursor: pointer;
    
    @media screen and (min-width: 768px) {
        margin-top: 43px;
        width: 255px;
        border: 1px solid var(--main-text-color);
    }

    @media screen and (min-width: 1280px) {
        margin-top: 45px;
        border: 1px solid var(--main-text-color);  
        font-size: 16px;
        line-height: 1.25;    
    }
}

.modal__more-button:hover,
.modal__more-button:focus {
    background-color: var(--main-text-color);
    color: var(--white-text-color);
    transition: color var(--animation), background-color var(--animation);
}
